body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.flex {
  display: -ms-flexbox;
  display: flex;
}
.column {
  -ms-flex-direction: column;
      flex-direction: column;
}
.h-center {
  -ms-flex-pack: center;
      justify-content: center;
}
.v-center {
  -ms-flex-align: center;
      align-items: center;
}

.contacts div {
  width: 50%;
  padding: 20px;
  margin: 10px;
  border-radius: 5px;
  background-color: rgba(0, 105, 172, 0.1);
}
.contacts div a {
  color: blue;

}
.contacts div a:hover {
  color: white;
  background-color: blue;
  border-radius: 5px;
}
.references div {
  width: 50%;
  padding: 20px;
  margin: 10px;
  border-radius: 5px;
  background-color: rgba(0, 105, 172, 0.1);

}
p {
  text-align: center;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  color: black;
}


a {
  margin: 20px;
  padding: 5px;
  text-decoration: none;
  font-size: 30px;
  color: black;
}

.portfolio {
  background-color: black;
}

h1{
  color: rgb(255, 240, 189);
  font-size: 100px;
  text-align: center;
  text-shadow: 7px 7px red, 14px 14px orange, 21px 21px yellow, 28px 28px green, 35px 35px blue, 42px 42px purple;
}

.selected {
  color: white;
  background-color: black;
  padding: 5px;
  border-radius: 10px;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}



